<template>
  <BaseLayout title="充值提现" bg="#1989fa" fg="#FFF" notitle>

    <van-row class="stat" gutter="2" justify="space-around" align="center">
      <van-col span="12">
        <div class="title"><van-tag size="large" color="#1989fa">累计充值</van-tag></div> 
        <number-roll :number="state.stat.deposit" fg="#FFF" size="21px"><span class="unit">元</span></number-roll>
      </van-col>
      <van-col span="12">
        <div class="title"><van-tag size="large" color="#1989fa">累计提现</van-tag></div>
        <number-roll :number="Math.abs(state.stat.withdrawal)" fg="#FFF" size="21px"><span class="unit">元</span></number-roll>
      </van-col>
    </van-row>

    <div class="info">

    <van-tabs v-model:active="state.active" color="#1989fa" offset-top="45" type="line" sticky swipeable animated @change="onChange">
      
      <van-tab title="充值">
        <div class="deposit">
          <van-field type="number" min="1" :border="1" v-model="state.num" input-align="right" clearable label="金额" placeholder="请输入充值金额">
            <template #extra>&nbsp;元</template>
          </van-field>
          <van-field v-model="state.remark" type="text" maxlength="20" input-align="right" label="备注" placeholder="可以不填" />
          <van-button round block  type="primary" @click="onQuery">立即充值</van-button>
        </div>
      </van-tab>

      <van-tab title="提现">

        <van-form class="withdrawal" @submit="onSubmit">
          <van-row align="center">
            <van-col span="7">
              收款账号：
            </van-col>
            <van-col span="17">
              <van-field v-model="state.account" type="textarea" maxlength="100" autosize rows="2" placeholder="银行账号和收款人姓名，请仔细核对，填错概不负责" />
            </van-col>
            <van-col span="7">
              提现数量：
            </van-col>
            <van-col span="17">
              <van-slider class="slider" v-model="state.amount" min="10" max="1000" step="1" active-color="#39F">
                <template #button>
                  <div class="slider-btn">{{ state.amount }}</div>
                </template>
              </van-slider>
            </van-col>
            <van-col span="24">
              <van-button round block type="primary" native-type="submit">申请提现</van-button>
            </van-col>
          </van-row>
        </van-form>

      </van-tab>

    </van-tabs>

    <van-divider class="title">操作历史</van-divider>

    <van-list v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
      <van-empty v-show="state.list.length == 0"></van-empty>
      
      <van-cell-group class="block" :border="false" v-for="(item,index) in state.list" :key="index">
        <van-cell class="dt" :border="false" :title="item.dt">
          <template #extra>
            <span :style="{color:item.color}">{{item.result}}</span>
            &nbsp;&nbsp;#{{item.id}}
            <van-icon class="close" v-show="item.status == 0" name="close" size="21" color="#F00" @click="onCancel(item.id)"/>
          </template>
        </van-cell>
        <van-cell class="item" :border="false" :title="item.remark" :value="item.amount + ' 元'"></van-cell>
        <van-cell class="detail" :border="false" :value="item.detail" is-link @click="$copy(item.detail)"></van-cell>
        <van-icon class="close" v-show="item.status == 0" name="close" size="21" @click="onCancel(item.id)"/>
      </van-cell-group>

    </van-list>

    </div>

  </BaseLayout>
</template>

<style scoped>
.stat {
  min-height: 90px;
  margin: 0 20px 10px 20px;
  background: #39F;
  border-radius: 5px;
  text-align: center;
}
.stat .title {
  margin: 5px 0;
}
.unit {
  font-size: 12px;
}


.info {
  min-height: 580px;
  padding: 10px;
  background: #F9F9F9;
  border-radius: 20px;
  border: solid 5px #1989fa;
}


.deposit, .withdrawal {
  margin-top: 20px;
  padding: 5px 15px;
  height: 180px;
  background: #FFF;
  border-radius: 10px;
  border: solid 1px #EEE;
  font-size: 14px;
}

.deposit {
  display:flex;
  flex-flow: column nowrap;
  justify-content: space-around;
}

.slider {
  margin:30px 10px 30px 0;
  width: 85%;
  float: right;
}
.slider-btn {
  width: 26px;
  color: #fff;
  font-size: 10px;
  line-height: 18px;
  text-align: center;
  background-color: #39F;
  border-radius: 100px;
}

.title {
  margin: 30px 5px 0px 5px;
  color: #999;
  border-color: #999;
  font-size: 14px;
}
.block {
  margin-top: 20px;
  padding-bottom: 10px;
  border-radius: 10px;
  overflow: hidden;
}
.dt {
  height: 50px;
  padding-top: 20px;
  font-size: 12px;
}
.item {
  height: 40px;
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
}
.detail {
  font-size: 12px;
  text-align: right;
}
.close {
  position: absolute;
  top: 0px;
  right: 0px;
}
</style>

<script>
import { reactive } from 'vue'
import { mapGetters } from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'
import NumberRoll from '@/components/NumberRoll.vue'

export default {
  components: {BaseLayout, NumberRoll},

  props: {
    action: {
      type: String,
      default: "deposit",
    },
  },

  setup(){
    const state = reactive({
      stat: {
        balance: 0,
        freez: 0,
        transfer: 0,
        deposit: 0,
        withdrawal: 0,
        consume: 0,
        reward: 0,
      },

      // 充值
      num: 1000,
      remark: "微信充值",

      // 提现
      account: "",
      amount: 0,

      active: 0, // 当前激活的标签
      type: "",
      loading: false,
      finished: false,

      list: [],   // 列表
      offset: 0,  // 记录偏移量
      limit : 20, // 每页记录数量
    });

    return {state};
  },

  created() {
    this.state.active = this.action == "withdrawal" ? 1 : 0;
    this.onChange();
  },

  mounted() {
    //this.$store.commit("toggleService", true);
  },

  computed: {
    ...mapGetters(["authorized", "account"]),
  },

  methods: {
    onChange() {
      this.state.type = ["deposit", "withdrawal"][this.state.active];
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
      this.$http.get("/property/summary?subject=cash").then((data) => this.state.stat = data);
    },
    onLoad() {
      this.$http.get("/property/cashlist?type=" + this.state.type + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            amount: item.amount.replace(/(\.\d\d)(\d+)$/, "$1"),
            result: ["待付款", "已完成", "被取消"][item.status],
            color : ["#999", "#090", "#F66"][item.status],
          }});
        }
      });
    },

    onQuery(){
      this.$http.post("/property/cashRequest?type=deposit&amount=" + this.state.num + "&remark=" + this.state.remark).then((data) => {

        data.code

        && this.$dialog.alert({
            title: "请用微信扫码付款",
            message: "<img src='http://api.qrserver.com/v1/create-qr-code/?size=250x250&ecc=H&margin=10&data=" + data.code + "'>",
            theme: "round-button",
            confirmButtonText: "我已完成付款",
            allowHtml: true,
          }).then(this.onChange)

        || (WeixinJSBridge && WeixinJSBridge.invoke("getBrandWCPayRequest", data, (res) => {
            if (res.err_msg == "get_brand_wcpay_request:ok"){
              this.$dialog.alert("付款成功");
              this.onChange();
            }
          }))

      }).catch(this.onChange);
    },

    onSubmit(){
      if (!/[\d]{10,}/.test(this.state.account))
        return this.$toast("银行账号不合要求");
      this.$http.post("/property/cashRequest?type=withdrawal&amount=" + this.state.amount + "&detail=" + this.state.account + "&remark=提现到银行卡").then(this.onChange);
    },

    onCancel(id) {
      this.$http.post("/property/cashCancel?id=" + id).then(this.onChange);
    },

  },
}
</script>
